$break-small: 320px;
$break-medium: 768px;

.searchbar {
  display: flex;
  padding: 10px;
  flex-direction: column;
  align-items: center;
  @media screen and (min-width: $break-medium) {
    flex-direction: row;
  }
}

.logo {
  padding: 5px;
  border-right: 2px solid #eeeeee; 
  width: 10%;
  display: none;
  @media screen and (min-width: $break-small) {
    display: block;
  }
}

.logo img {
  width: 100%;
  height: auto;
}

.icon {
  background-image: url('http://i47.tinypic.com/r02vbq.png');
}

.searchInput {
  padding: 5px;
  width: 90%
}

.searchInput input {
  width: 100%;
  height: 100%;
}
